/* @subsection Adaptive font */
@media (min-width: 320px) and (max-width: 959px) {
	.fontname {
		font-size: calc( 24px + 24 * (100vw - 320px) / (960 - 320) );
		/* При отрицательном наклоне нужно инвертировать контрольные точки */
		line-height: calc( 140% + 6.4 * (100vw - 960px) / (320 - 960) ); } }

/**
 * @section Print
 */
/* Регулировка стиля для устройств с высоким разрешением */
// @media print, (-webkit-min-device-pixel-ratio: 1.25), (min-resolution: 1.25dppx), (min-resolution: 120dpi)


/* ==========================================================================
	Print styles.
	Inlined to avoid the additional HTTP request:
	http://www.phpied.com/delay-loading-your-print-css/
	@mixin ========================================================================= */

@media print {
	*, *:before, *:after {
		background: transparent!important;
		color: #000!important;
		box-shadow: none!important;
		text-shadow: none!important; } }
@media print {
	a, a:visited {
		text-decoration: underline; }
	a[href]:after {
		content: " (" attr(href) ")"; }
	abbr[title]:after {
		content: " (" attr(title) ")"; }
	.container {
		width: auto; }

	a[href^="#"]:after, a[href^="javascript:"]:after {
		content: ""; }
	pre {
		white-space: pre-wrap !important; }
	pre,
	blockquote {
		border: 1px solid #999;
		page-break-inside: avoid; }

	thead {
		display: table-header-group; }
	tr, img {
		page-break-inside: avoid; }
	p, h2, h3 {
		orphans: 3;
		widows: 3; }
	h2, h3 {
		page-break-after: avoid; } }
